<template>
    <div id='gymbranch' class="addgymbranch">
        <div class='top'>
          <span class='fundInfo' @click='goback()'></span>
          <img src='../../assets/images/icon_nav_arrow.png'/>
          <span class='branch'>{{text}}</span>
        </div>
        <el-form ref="form" :model="form" label-width="80px" :rules='rules'>
            <div class="formLeft">
              <el-form-item label="" class='formNew Doit' prop="name">
                <el-input v-model="form.name" class='inputNew' placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" class='formNew'>
                  <el-input v-model="form.shortName" class='inputNew' placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" class='TextArea region Doit' prop="brief">
                  <el-input v-model="form.brief" class='inputNew'  type="textarea" placeholder="" maxlength="2999"></el-input>
              </el-form-item>
              <p class="selectCity">: <span v-if="tips2">{{tips2}}</span></p>
              <el-upload
                  :limit = 1
                  list-type="picture-card"
                  :class="{upload: true, picShow: fileList.length > 0 ? true : false}"
                  :on-success='handlesuccess'
                  :http-request='upload'
                  :action='actionUrl'
                  :file-list='fileList'
                  :before-upload="beforeUpload"
                  :on-remove="handleRemove"
                  >
                  <img src='../../assets/images/btn_add_pic.png' v-if='isShow'>
                  <div slot="tip" class="el-upload__tip">
                      <ul>
                          <li>jpg/png</li>
                          <li>1M</li>
                      </ul>
                  </div>
              </el-upload>
              <el-form-item label="" class='formNew Doit' prop="contact">
                  <el-input v-model.number="form.contact" class='inputNew' maxlength = '11' placeholder=""></el-input>
              </el-form-item>
              <!---->
              <p class="selectCity"><span v-if="tips1">{{tips1}}</span></p>
              <!-- <area-select :level="2" v-model='address' type="text" :data="data" :placeholders="placeholder">
              </area-select> -->
              <div class="addressWrap">
                  <el-select v-model="form.province" placeholder="" clearable v-on:change="getProv($event)" class='sel'>
                    <el-option v-for="item in provs" :key="item.name" :label="item.name" :value="item.name">
                      {{item.name}}<img src='../../assets/images/icon_right.png'/>
                    </el-option>
                  </el-select>
                  <el-select v-model="form.city" placeholder="" clearable v-on:change="getCity($event)" class='sel'>
                    <el-option v-for="item in citys" :key="item.name" :label="item.name" :value="item.name">
                      {{item.name}}<img src='../../assets/images/icon_right.png'/>
                    </el-option>
                  </el-select>
                   <el-select v-model="form.district" placeholder="" clearable class='sel'>
                    <el-option v-for="item in distinctlist" :key="item.name" :label="item.name" :value="item.name">
                      {{item.name}}<img src='../../assets/images/icon_right.png'/>
                    </el-option>
                  </el-select>
              </div> 
                 <!-- <el-select v-model="form.province" class='formNew'  placeholder="" clearable>
                        <el-option
                        v-for="(item, index) in provs"
                        :key="index"
                        :label="item.name"
                        :value="item.name"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}">
                        {{item}}{{provs}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                  </el-select> -->
              <el-form-item label="" class='formNew Doit latLon' prop="coordinate">
                  <el-input v-model="form.coordinate" class='inputNew' placeholder=""></el-input>
                  <a class='target' href='https://lbs.amap.com/console/show/picker' target='_blank'></a>
              </el-form-item>
              <p class="positionTips">("")</p>
              <el-form-item class='TextArea region detail'>
                  <el-input v-model="form.address" class='inputNew'  type="textarea" placeholder=""></el-input>
              </el-form-item>
            </div>
            <div class="formRight">
              <el-form-item label="" class='TextArea region'>
                  <el-input v-model="form.notes" class='inputNew'  type="textarea" placeholder=""></el-input>
              </el-form-item>
            </div>
            <div class='btn2'>
                <div class='cancle' @click="cancle"></div>
                <div class='submit' @click="submit('form')"></div>
            </div>
        </el-form>
    </div>
</template>
<script>
import json from '../../assets/address.json'
import {addbranch, updatebranch, upload} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage} from '../../utils/com.js'
import { setTimeout } from 'timers';
export default {
  data () {
    var checkRegion = (rule, value, callback) => {
      if (value) {
        let FormatReg = /^[^|]*$/
        // let FormatReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
        if (!FormatReg.test(value)) {
          callback(new Error('|'))
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
    var checkContact = (rule, value, callback) => {
      if (value) {
        let FormatReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
        if (!FormatReg.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      } else {
        callback(new Error(''))
      }
    }
    return {
      form: {
        name: '',
        brief: '',
        shortName: '',
        province: '',
        city: '',
        motes: '',
        contact: '',
        region: '',
        address: '',
        pictures: [],
        province: '',
        city: '',
        district: ''
      },
      provs: [],
      address: [],
      citys: [],
      distinctlist: [],
      selectProv: '',
      selectCity: '',
      rules: {
        name: [{required: true, message: '', trigger: 'blur'}],
        brief: [{required: true, message: '', trigger: 'blur'}],
        // region: [{validator: checkRegion, trigger: 'blur'}],
        contact: [{validator: checkContact, trigger: 'blur'}],
        coordinate: [{required: true, message: '', trigger: 'blur'}]
      },
      modify: false,
      text: '',
      list: [],
      mid: '',
      pictures: [],
      actionUrl: 'https://mainapi.com/gymmanage/image/upload',
      fileList: [],
      isShow: true,
      placeholder: ['', '', ''],
      options: [],
      detailaddress: '',
      onlyadd: false,
      tips1: '',
      tips2: '',
      province: '', // 
      city: '' // 
    }
  },
  created () {
    this.$nextTick(_ => {
      this.provs = json
      this.initAddress()
      this.modify = this.$route.query.modify
      if (this.modify) {
        this.form = getSessionStorage('detailInfo', {}) // 
        // 
        this.province = this.form.province
        this.city = this.form.city
        this.citys = []
        this.selectCity = ''
        if (this.form.province) {
          let allCity = this.provs
          for (var val of allCity) {
            if (this.form.province === val.name) {
              this.citys = val.districts
            }
          }
        }
        if (this.form.city && this.citys) {
          this.distinctlist = []
          this.selectCity = ''
          let allCity = this.citys
          for (var val of allCity) {
            if (this.form.city === val.name) {
              this.distinctlist = val.districts
            }
          }
        }
        if (this.form.pictures && this.form.pictures.length > 0) {
          for (var i = 0; i < this.form.pictures.length; i++) {
            this.fileList.push({name: 'bic.jpeg', url: this.form.pictures[i]})
          }
        }
        console.log('', this.fileList)
        this.text = ''
      }
    })
    // console.log('', zone.proviceList)
  },
  watch: {
    'mid': function () {
      this.form.city = this.mid
      console.log('', this.mid)
    },
    'fileList': function () {
      if (this.fileList.length > 0) {
        this.isShow = false
      } else {
        this.isShow = true
      }
    }
  },
  methods: {
    // 
    handlesuccess (file, fileList) {
      console.log('')
    },
    handleRemove (file, fileList) {
      this.fileList = []
    },
    // 
    getProv: function (prov) {
      this.citys = []
      this.selectCity = ''
      if (this.province && this.province !== prov) {
        this.form.city = ''
        this.form.district = ''
      }
      this.province = prov
      let allCity = this.provs
      for (var val of allCity) {
        if (prov === val.name) {
          this.citys = val.districts
        }
      }
    },
    // 
    getCity: function (city) {
      this.distinctlist = []
      this.selectCity = ''
      if (this.city && this.city !== city) {
        this.form.district = ''
      }
      this.city = city
      let allCity = this.citys
      for (var val of allCity) {
        if (city === val.name) {
          this.distinctlist = val.districts
        }
      }
    },
    async submit (ruleForm) {
      this.checkFile()
      let self = this
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          if (!this.modify) {
            if (this.tips1 || this.tips2) {
              return
            }
            if (this.fileList && this.fileList.length > 0) {
              let list = []
              list.push(this.fileList[0].url)
              this.form.pictures = list
            } else {
              this.form.pictures = []
            }
            // this.form.address = this.form.province + this.form.city + '|' + this.form.region
            addbranch(this.form).then(function (res) {
              if (isApiResponseOk(res)) {
                self.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
                self.$router.push({name: 'gymlist'})
              }
            })
          } else {
            if (this.tips1 || this.tips2) {
              return
            }
            if (this.fileList && this.fileList.length > 0) {
              let list = []
              list.push(this.fileList[0].url)
              this.form.pictures = list
            } else {
              this.form.pictures = []
            }
            // this.form.address = this.form.province + '|' + this.form.city + '|' + this.form.region
            updatebranch(this.form).then(function (res) {
              if (isApiResponseOk(res)) {
                self.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
                self.$router.push({name: 'gymlist'})
              }
            })
          }
        }
      })
    },
    // 
    goback () {
      this.$router.push({name: 'gymlist'})
    },
    cancle () {
      this.$router.push('gymlist')
    },
    // 
    async upload (item, fileList) {
      console.log('', item.file)
      let formData = new FormData()
      formData.append('image', item.file)
      console.log('', formData)
      const res = await upload(formData)
      if (isApiResponseOk(res)) {
        this.fileList.push({name: 'bic.jpeg', url: res.data})
        this.$message({
          showClose: true,
          message: ',',
          type: 'success'
        })
      } else {
        this.$message({
          message: '',
          type: 'warning'
        })
      }
    },
    beforeUpload(file) {
        const isMatch = file.type === 'image/jpeg' || file.type === 'image/jpg' || file.type === 'image/png'
        const isLt2M = file.size / 1024 / 1024 < 1
        if (!isMatch) {
            this.$message({
              message: ' JPG/PNG !',
              type: 'warning'
            })
        }
        if (!isLt2M) {
            this.$message({
              message: ' 1M!',
              type: 'warning'
            })
        }
        return isMatch && isLt2M
    },
    checkFile () {
      if (!this.form.province || !this.form.city || !this.form.district) {
        this.tips1 = ''
      } else {
        this.tips1 = ''
      }
      if (this.fileList.length == 0) {
        this.tips2 = ''
      } else {
        this.tips2 = ''
      }
    },
    // 
    initAddress () {
      
      // console.log('', this.provs, this)
      // for (var i = 0;i < json.length; i++) {
      //   this.provs.push({label: json[i].name, value: json[i].name})
      // }
    },
    test () {

    }
  }
}
</script>
<style lang="sass" scoped>
#gymbranch
    margin-bottom: .2rem;
    .btn
        display: flex;
        justify-content: center;
        .submit
            background: #20C6BA;
            border-radius: .04rem;
            color: #fff;
            font-size: .16rem;
            width: 1.2rem;
            height: .44rem;
            line-height: .44rem;
            color: #fff;
            text-align: center;
            font-size: .16rem;
            margin-top: .3rem;
            cursor: pointer;
    .top
        margin-bottom: .2rem;
        display: flex;
        align-items: center;
        .fundInfo
            font-family: PingFangSC-Semibold;
            font-size: .20rem;
            color: #8597A1;
            cursor: pointer;
        .branch
            font-family: PingFangSC-Semibold;
            font-size: .24rem;
            color: #263238;
        img
            width: .16rem;
            margin-left: .12rem;
            margin-right: .2rem;
    .addressWrap
          width: 5.08rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .el-select.sel
            width: 32%;
    .latLon
      position: relative;
      .target
        position: absolute;
        top: 0;
        right: -1rem;
        height: 0.45rem;
        line-height: 0.45rem;        

</style>
<style>
#gymbranch.addgymbranch .el-form .el-form-item{
  background: #FBFBFC;
  border: 1px solid #EAECEF;
  width: 5rem;
  margin-bottom: 24px;
  height: 0.5rem;
  padding: 0;
  padding-left: 0.2rem;
}
#gymbranch.addgymbranch .el-form .el-form-item.TextArea{
  height: 1.2rem;
}
#gymbranch.addgymbranch .el-form .el-form-item .el-form-item__label{
  margin: 0;
  line-height: 0.5rem;
  font-size: 0.14rem;
}
#gymbranch.addgymbranch .el-form .el-form-item .el-form-item__label::before{
  content: ''
}
#gymbranch.addgymbranch .Doit .el-form-item__label:after, .selectCity::after{
  content: '*';
  color: #f56c6c;
  margin-right: 4px;
  position: relative;
  top: 2px;
  left: 5px;
}
#gymbranch.addgymbranch .positionTips{
    font-size: 0.12rem;
    color: #8597A1;
    text-align: right;
    width: 5rem;
    margin: 0;
    position: relative;
    transform: translateY(-0.1rem);
}
.addgymbranch .latLon .el-form-item__error{
  left: 130%;
  line-height: 0.38rem;
}
.addgymbranch .el-form-item__error {
  color: #f56c6c;
  font-size: 12px;
  line-height: 1;
  padding-top: 4px;
  position: absolute;
  left: 102%;
  display: inline-block;
  white-space: nowrap;
  line-height: 0.4rem;
  top: 0;
}
#gymbranch.addgymbranch .el-form .TextArea .el-textarea{
  margin: 0;
  height: 1rem;
}
#gymbranch.addgymbranch .el-form .TextArea .el-textarea .el-textarea__inner{
  background: #FBFBFC;
  font-family: PingFangSC-Medium;
  font-size: 0.16rem;
  color: #263238;
  letter-spacing: 0;
  text-align: justify;
  line-height: 0.24rem;
  padding-top: 0.1rem;
  text-align: right;
}
#gymbranch.addgymbranch .el-input{
  height: 0.46rem;
  line-height: normal;
}
#gymbranch.addgymbranch .el-input .el-input__inner{
  height: 0.46rem!important;
  margin: 0!important;
  line-height: 0.46rem!important;
  background: #FBFBFC;
  font-family: PingFangSC-Medium;
  font-size: 0.16rem;
  color: #263238;
  text-align: right;
}
#gymbranch.addgymbranch .selectCity{
  margin-bottom: 0;
  font-size: 0.14rem;
  position: relative;
  height: 0.2rem;
}
#gymbranch.addgymbranch .selectCity span{
  position: absolute;
  left: 100%;
  white-space: nowrap;
  top: 0.4rem;
  color: #f56c6c;
  font-size: 12px;
}
#gymbranch.addgymbranch .area-select-wrap {
  margin: 0.1rem 0;
  display: flex;
  justify-content: space-between;
  width: 5rem;
  align-items: center;
}
#gymbranch.addgymbranch .area-select-wrap .area-select{
  height: 0.4rem;
}
#gymbranch.addgymbranch .area-select-wrap .area-select .area-selected-trigger{
  line-height: 0.4rem;
  font-size: 0.16rem;
  color: #263238;
}

#gymbranch.addgymbranch .upload{
  width: 5rem;
  margin: 0.2rem 0;
  display: flex;
}
#gymbranch.addgymbranch .upload .el-upload--picture-card, #gymbranch.addgymbranch .upload .el-upload--picture-card img{
  width: 1.2rem;
  height: 0.8rem;
  border: 0;
  display: flex;
}
#gymbranch.addgymbranch .el-upload-list--picture-card .el-upload-list__item{
  width: 1.2rem;
  height: 0.8rem;
}
#gymbranch.addgymbranch .picShow .el-upload--picture-card, #gymbranch.addgymbranch .picShow .el-upload__tip{
  display: none;
}
#gymbranch.addgymbranch .el-upload__tip{
  margin: 0;
  width: 1.2rem;
  height: 0.8rem;
  position: relative;
}
#gymbranch.addgymbranch .el-upload__tip ul{
  margin: 0;
  position: absolute;
  bottom: 0;
  font-size: 10px;
  color: #8597A1;
  white-space: nowrap;
}
#gymbranch.addgymbranch .el-form{
  display: flex;
}
#gymbranch.addgymbranch .el-form .formRight{
  margin-left: 2rem;
}
.addgymbranch{
  position: relative;
}
.addgymbranch .btn2{
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 0.2rem;
  left: 50%;
  transform: translateX(-50%);
  width: 2.6rem;
}
.addgymbranch .btn2 .submit, .addgymbranch .cancle{
  background: #20C6BA;
  border-radius: .04rem;
  color: #fff;
  font-size: .14rem;
  width: 1.2rem;
  height: .44rem;
  line-height: .44rem;
  color: #fff;
  text-align: center;
  margin-top: .8rem;
  cursor: pointer;
}
.addgymbranch .cancle{
 background: #C0C4CC;
}
a{
  text-decoration: none;
  font-size: .16rem;
}
</style>




